import React, { useState } from 'react';

const ToDoItem = ({ todoItem, handleDelete, handleUpdate, todoList, setTodoList }) => {
  const { id, title, done, updatedAt, createdAt } = todoItem;
  const [updatedTitle, setUpdatedTitle] = useState(title);

  const onToggle = async () => {
    const updatedItem = { ...todoItem, done: !done };
    await handleUpdate(id, updatedItem);
    // API ìš”ì²­ì´ ì™„ë£Œëœ í›„ì— ìƒíƒœ ì—…ë°ì´íŠ¸
    const updatedList = todoList.map((item) =>
      item.id === id ? { ...item, done: !item.done } : item
    );
    setTodoList(updatedList);
  };



  const onUpdate = async () => {
    const updatedItem = { ...todoItem, title: updatedTitle };
    await handleUpdate(id, updatedItem);
    // API ìš”ì²­ì´ ì™„ë£Œëœ í›„ì— ìƒíƒœ ì—…ë°ì´íŠ¸
    const updatedList = todoList.map((item) =>
      item.id === id ? { ...item, title: updatedTitle } : item
    );
    setTodoList(updatedList);
  };

  const onDelete = () => {
    handleDelete(id);
  };

  const handleKeyDown = (e) => {
    if (e.key === 'Enter') {
      onUpdate();
    }
  };

  const handleBlur = () => {
    onUpdate();
  };

  return (

    <li className={`ToDoItem ${done ? 'completed' : 'pending'}`}>
      <div className='input'>
        <input className='checkbox' type="checkbox" checked={done} onChange={onToggle} />
        <input className='inputbox'
          type="text"
          value={updatedTitle}
          onChange={(e) => setUpdatedTitle(e.target.value)}
          onKeyDown={handleKeyDown}
          onBlur={handleBlur} // ì…ë ¥ì´ ëë‚˜ë©´ onUpdate í˜¸ì¶œ
          disabled={done} // ì²´í¬ë˜ì–´ ìˆìœ¼ë©´ ë¹„í™œì„±í™”
          style={{ textDecoration: done ? 'line-through' : 'none' }} // ì²´í¬ë˜ì–´ ìˆìœ¼ë©´ ì·¨ì†Œì„  ì¶”ê°€
        />
        <button className='oneDelete' onClick={onDelete}>ì‚­ì œ</button>
      </div>
      <p className="ToDoItem-updatedAt">{new Date(createdAt).toLocaleString('ko-KR', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: 'numeric',
        minute: 'numeric',
        hour12: true,
      })} ë“±ë¡</p>
      <p className="ToDoItem-updatedAt">{new Date(updatedAt).toLocaleString('ko-KR', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: 'numeric',
        minute: 'numeric',
        hour12: true,
      })} ìˆ˜ì •</p>
    </li>
  );
};

export default ToDoItem;
