import React, { useEffect, useRef } from 'react';
import ToDoItem from './TodoItem';
import Sortable from 'sortablejs';

const ToDoItemList = ({ todoList, setTodoList, setIsLoading }) => {
  const sortableRef = useRef(null);
  const debouncedUpdateOrder = useRef(null);

  const getTodo = async () => {
    try {
      setIsLoading(true) // ë¡œë”© ì‹œì‘
      const res = await fetch(
        'https://asia-northeast3-heropy-api.cloudfunctions.net/api/todos',
        {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            apikey: 'KDT5_nREmPe9B',
            username: 'KDT5_ParkYoungWoong',
          },
        }
      );
      const json = await res.json();
      setTodoList(json);
    } catch (error) {
      console.error('í•  ì¼ ëª©ë¡ì„ ê°€ì ¸ì˜¤ëŠ” ì¤‘ì— ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤:', error);
    } finally {
      setIsLoading(false) // ë¡œë”© ë
    }
  };


  const deleteTodo = async (id) => {
    try {
      setIsLoading(true) // ë¡œë”© ì‹œì‘
      await fetch(
        `https://asia-northeast3-heropy-api.cloudfunctions.net/api/todos/${id}`,
        {
          method: 'DELETE',
          headers: {
            'Content-Type': 'application/json',
            apikey: 'KDT5_nREmPe9B',
            username: 'KDT5_ParkYoungWoong',
          },
        }
      );
      setTodoList((prevList) => prevList.filter((item) => item.id !== id));
    } catch (error) {
      console.error('í•  ì¼ì„ ì‚­ì œí•˜ëŠ” ì¤‘ì— ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤:', error);
    } finally {
      setIsLoading(false) // ë¡œë”© ë
    }
  };

  const deleteCompletedTodos = async () => {
    try {
      setIsLoading(true) // ë¡œë”© ì‹œì‘
      const completedIds = todoList
        .filter((item) => item.done)
        .map((item) => item.id);
      await Promise.all(
        completedIds.map((id) =>
          fetch(
            `https://asia-northeast3-heropy-api.cloudfunctions.net/api/todos/${id}`,
            {
              method: 'DELETE',
              headers: {
                'Content-Type': 'application/json',
                apikey: 'KDT5_nREmPe9B',
                username: 'KDT5_ParkYoungWoong',
              },
            }
          )
        )
      );
      setTodoList((prevList) => prevList.filter((item) => !item.done));
    } catch (error) {
      console.error('ì™„ë£Œëœ í•  ì¼ì„ ì‚­ì œí•˜ëŠ” ì¤‘ì— ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤:', error);
    } finally {
      setIsLoading(false) // ë¡œë”© ë
    }
  };



  const updateTodo = async (id, updatedData) => {
    try {
      setIsLoading(true); // ë¡œë”© ì‹œì‘
      const res = await fetch(
        `https://asia-northeast3-heropy-api.cloudfunctions.net/api/todos/${id}`,
        {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
            apikey: 'KDT5_nREmPe9B',
            username: 'KDT5_ParkYoungWoong',
          },
          body: JSON.stringify(updatedData),
        }
      );
      if (res.ok) {
        const updatedList = todoList.map((item) =>
          item.id === id ? { ...item, ...updatedData } : item
        );
        setTodoList(updatedList);
      } else {
        console.error('í•  ì¼ì„ ì—…ë°ì´íŠ¸í•˜ëŠ” ì¤‘ì— ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤:', res.status);
      }
    } catch (error) {
      console.error('í•  ì¼ì„ ì—…ë°ì´íŠ¸í•˜ëŠ” ì¤‘ì— ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤:', error);
    } finally {
      setIsLoading(false); // ë¡œë”© ë
    }
  };





  useEffect(() => {
    getTodo();
  }, []);

  const debouncedUpdateTodoOrder = async (updatedOrder) => {
    try {
      setIsLoading(true);
      await fetch(
        'https://asia-northeast3-heropy-api.cloudfunctions.net/api/todos/reorder',
        {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
            apikey: 'KDT5_nREmPe9B',
            username: 'KDT5_ParkYoungWoong',
          },
          body: JSON.stringify({
            todoIds: updatedOrder,
          }),
        }
      );
    } catch (error) {
      console.error('í•  ì¼ ìˆœì„œë¥¼ ì—…ë°ì´íŠ¸í•˜ëŠ” ì¤‘ì— ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤:', error);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    if (sortableRef.current) {
      Sortable.create(sortableRef.current, {
        onEnd: ({ oldIndex, newIndex }) => {
          setTodoList((prevList) => {
            const updatedList = [...prevList];
            const [movedItem] = updatedList.splice(oldIndex, 1);
            updatedList.splice(newIndex, 0, movedItem);

            // ì´ì „ì— ì˜ˆì•½ëœ ë””ë°”ìš´ì‹±ëœ í•¨ìˆ˜ ì·¨ì†Œ
            clearTimeout(debouncedUpdateOrder.current);

            // ì¼ì • ì‹œê°„ í›„ì— ìˆœì„œ ì—…ë°ì´íŠ¸ í•¨ìˆ˜ í˜¸ì¶œ
            debouncedUpdateOrder.current = setTimeout(() => {
              const updatedOrder = updatedList.map((item) => item.id);
              debouncedUpdateTodoOrder(updatedOrder);
            }, 500); // 500ms ë””ë°”ìš´ìŠ¤ ì§€ì—° ì‹œê°„

            return updatedList;
          });
        },
      });
    }
  }, [todoList]);


  const completedList = todoList.filter((todoItem) => todoItem.done);
  const incompleteList = todoList.filter((todoItem) => !todoItem.done);

  return (
    <div className="TodoItemList">
      <p className="TodoItemList-Title">ì§„í–‰</p>
      <ul ref={sortableRef} className="TodoItemList-Item">
        {incompleteList.length > 0 ? (
          incompleteList.map((todoItem) => (
            <ToDoItem
              key={todoItem.id}
              todoItem={todoItem}
              setTodoList={setTodoList}
              handleDelete={deleteTodo}
              handleUpdate={updateTodo}
              todoList={todoList}
            />
          ))
        ) : (
          <li>{completedList.length > 0 ? (<span>í•  ì¼ì„ ëª¨ë‘ ëë‚¸ ë‹¹ì‹ ,<br />ì •ë§ í›Œë¥­í•´ìš”! ğŸ‘ğŸ¼ğŸ‘ğŸ¼ğŸ‘ğŸ¼<br /></span>) : (<span>ìƒˆë¡œìš´ í•  ì¼ì„ ë“±ë¡í•´ ë³´ì„¸ìš”!</span>)}</li>

        )}
      </ul>
      {completedList.length > 0 && (
        <div className='completed'>
          <p className="TodoItemList-Title">ì™„ë£Œ</p>
          <ul className="TodoItemList-Item">
            {completedList.map((todoItem) => (
              <ToDoItem
                key={todoItem.id}
                todoItem={todoItem}
                setTodoList={setTodoList}
                handleDelete={deleteTodo}
                handleUpdate={updateTodo}
                todoList={todoList}
              />
            ))}
          </ul>
          <button className='completedListDelete' onClick={deleteCompletedTodos}> ì™„ë£Œ í•­ëª© ì‚­ì œ</button>
        </div>
      )}
    </div>
  );
};

export default ToDoItemList;
